const login = ()=>{
    return (dispatch)=>{
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login';
        fetch(url, {
            method: 'get',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then(res => res.json())
            .then(res => {
                dispatch({
                    type: 'LOGIN',
                    username: res.data.name
                })
                console.log(res)
            }
            )
    }
}

const getData = ()=>{
    return (dispatch)=>{
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then(res => res.json())
            .then(res => {
                console.log(res);
                dispatch({
                    type: 'GETDATA',
                    datalist: res.data
                })
            }
            )
    }
}
function shouCang(id){
    return {
        type:"SHOUCANG",
        shoucang:id
    }
}
function delShouCang (id){
    return {
        type:"DEL",
        del:id
    }
}
export {getData,shouCang,delShouCang,login};