import React from 'react'
import { connect } from 'react-redux';
import ShouCangSize from './ShouCangSize'
const MapShouCang = (props) => {
    console.log(props)
    return <div>
        {
            props.shoucang.map((item, key) => {
                let help = props.datalist.map(element => {
                    if (item == element.id) {
                        console.log(1);
                        return <ShouCangSize data={element} dispatch={props.dispatch} key={key} />
                    }
                });

                return help;

            })
        }
    </div>
}

export default connect(store => store)(MapShouCang);