import React, { Component,useEffect } from 'react'
import { Flex } from 'antd-mobile';
import './home.css'
import Lunbotu from './Lunbotu';
import MapPic from './MapPic';
import { connect } from 'react-redux';
import { getData } from '../redux/actionCreators';
const Home = (props) => {
  const getlist = () => {
    props.dispatch(getData());
  }
  useEffect(() => {
    getlist();
  }, []);
  //此处解决了循环请求datalist问题
  return (
    <div className="flex-container">
      <Flex className='palaceA'>
        <i className="item1" className="iconfont icon-iconfonttaobao" style={{ color: 'white', fontSize: 30 + 'px', paddingRight: 12 + 'px' }}></i>
        <input type="text" placeholder="寻找宝贝店铺" className="item2" onClick={() => { props.history.push('/home/search') }} style={{ color: 'white' }}></input>
      </Flex>

      <Flex>
        <Lunbotu />
      </Flex>

      <Flex>
        <MapPic history={props.history} />
      </Flex>
    </div>
  );
}
export default connect((store) => {
  return store;
})(Home);