import React, { useEffect } from 'react';
import { useState } from 'react';
import { Flex } from 'antd-mobile';
import { connect } from 'react-redux'
import './shoptab.css'
import { shouCang, delShouCang } from '../redux/actionCreators'
import store from '../redux/store';
const ShopTab = (props) => {
    let flag = 'iconfont icon-shoucang';
    props.shoucang.forEach((item) => {
        if (item == props.id) {
            flag = 'iconfont icon-shoucang1'
        }
    })
    const [now, setNow] = useState(flag);
    return (
        <div className='flex-container' style={{ flexWrap: 'wrap' }} className='a'>
            <div className='flex-container tubiao'>
                <Flex style={{ paddingLeft: 4 + 'px' }}><i className="iconfont icon-dianpu" style={{ fontSize: 16 + 'px', marginBottom: 8 + 'px' }}></i></Flex>
                <Flex>店铺</Flex>
            </div>
            <div className='flex-container tubiao'>
                <Flex style={{ paddingLeft: 4 + 'px' }}><i className="iconfont icon-kefu" style={{ fontSize: 16 + 'px', marginBottom: 8 + 'px' }}></i></Flex>
                <Flex>客服</Flex>
            </div>
            <div className='flex-container tubiao'
                onClick={() => {
                    console.log(now);
                    if (props.userinfo == '') {
                        props.history.push('/login');
                    } else {
                        if (now === "iconfont icon-shoucang") {
                            setNow('iconfont icon-shoucang1');
                            localStorage.setItem(`${props.id}`, props.id);
                            props.dispatch(shouCang(props.id));
                        }
                        if (now === "iconfont icon-shoucang1") {
                            setNow('iconfont icon-shoucang');
                            props.dispatch(delShouCang(props.id));
                        }
                    }
                }} >
                <Flex style={{ paddingLeft: 4 + 'px' }} ><i className={now} style={{ fontSize: 16 + 'px', marginBottom: 8 + 'px' }}></i></Flex>
                <Flex>收藏</Flex>
            </div>
            <div className='flex-container but'>
                <Flex>
                    <button className='size size1'>加入购物车</button>
                    <button className='size size2'>立即购买</button>
                </Flex>
            </div>
        </div>
    )
}
export default connect(store => store)(ShopTab);
