import React, { Component } from 'react'
import { NavBar, Icon, Flex } from 'antd-mobile';
import ShopTab from './ShopTab';
import { connect } from 'react-redux'
import store from '../redux/store';
class Detail extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        console.log(this.props);
        let title,pic;
        const id = localStorage.getItem("id");
        this.props.datalist.forEach(item => {
            if (item.id == id) {
                title = item.title;
                pic = item.img;
            }
        });
        return (
            <div>
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={() => this.props.history.goBack()}
                >
                    {title}
                </NavBar>
                <img src={pic} style={{ width: 100 + '%' }}></img>
                <Flex style={{ backgroundColor: 'white', position: 'fixed', bottom: 0, width: 100 + '%', paddingLeft: 4 + 'px' }}>
                    <ShopTab id={id} userinfo={this.props.userinfo} history={this.props.history}/>
                </Flex>

            </div>
        )
    }
}
export default connect((store) => store)(Detail);