import React,{ Component } from 'react'
import { SearchBar} from 'antd-mobile';
const Search = (props) => {
  return (
    <div>
      <SearchBar
        placeholder="Search"
        onClear={()=>{
          props.history.goBack();
        }}
        showCancelButton
      />
    </div>
  )
}


export default Search;