let data =
{
    datalist: [],
    shoucang: [],
    userinfo: ''
};

console.log(data);
const homeReducer = (state = data, action) => {
    console.log(state);
    switch (action.type) {
        case 'GETDATA':
            if (state.datalist.length==0) {
                return Object.assign({}, state, { datalist: action.datalist });
            } else {
                return state;
            }
        case 'SHOUCANG':
            if (state.shoucang.every((item) => {
                return item != action.shoucang
            })) {
                return Object.assign({}, state, { shoucang: [...state.shoucang, action.shoucang] });
            }
            return state;
        case 'DEL':
            let newstate = state.shoucang.filter((item) => {
                if (item == action.del) {
                    return false;
                }
                return true;
            })
            return Object.assign({}, state, { shoucang: newstate });
        case 'LOGIN':
            return Object.assign({}, state, { userinfo: action.username });
        default:
            return state;
    }
}
export default homeReducer;
 