import React from 'react'
import {connect} from 'react-redux'
import {login} from '../redux/actionCreators.js'
const Login = (props) => {
    console.log(props);
    if(props.userinfo!=''){
        props.history.go(-1);
    }
    const loginNow = ()=>{
        props.dispatch(login());
        alert("正在登录，请耐心等待");
    }
    return (
        <div>
            <button onClick={loginNow}>登录</button>
        </div>
    )
}

export default connect(store=>store)(Login)

