import React from 'react'
import { Flex } from 'antd-mobile';
import './mappic.css'
import { connect } from 'react-redux'


const GoodSize = (props) => {
    return (
        <div className="flex-container" className='container' style={{ float: 'left', marginRight: 8 + 'px', marginBottom: 10 + 'px' }}
            onClick={() => {
                props.history.push('/home/detail');
                localStorage.setItem("id", props.data.id);
            }
            }
        >
            <Flex className='a'>
                <img src={props.data.img} className='shopImg' ></img>
            </Flex>
            <Flex className='a'>
                {props.data.title}
            </Flex>
            <Flex style={{ color: 'orange' }} className='a'>
                ￥ {props.data.price}
            </Flex>
        </div>
    )
}
const MapPic = (props) => {
    console.log(props);
    return <div className="flex-container" style={{ flexWrap: 'wrap', paddingLeft: 15 + 'px', marginTop: 10 + 'px' }}>

        {
            props.datalist.map((item,key) => {
                return <GoodSize data={item} history={props.history} id={item.id} key={key} />
            })
        }

    </div>
}
export default connect((store) => {
    return {
        datalist: store.datalist
    };
})(MapPic);

