import React from 'react';
import { Button, TabBar } from 'antd-mobile'
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import store from '../redux/store';
class Mytab extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			selectedTab: 'blueTab'
		};
	}
	render() {
		console.log(this.props);
		const { pathname } = this.props.location;
		if (pathname.split('/').length > 2) {
			return null;
		}
		if(pathname=='/login'){
			return null;
		}
		return (
			<div style={{ position: 'fixed', width: '100%', bottom: 0 }}>
				<TabBar
					unselectedTintColor="#949494"
					tintColor="red"
					barTintColor="white"
				>
					<TabBar.Item
						title="首页"
						key="home"
						icon={<i className='iconfont icon-shouyeshouye'></i>
						}
						selectedIcon={
							<i className="iconfont icon-shouye"></i>
						}
						selected={pathname === '/'}
						onPress={() => {
							this.props.history.push('/');
						}}
						data-seed="logId"
					>

					</TabBar.Item>
					<TabBar.Item
						icon={
							<i className='iconfont icon-shoucang'></i>
						}
						selectedIcon={
							<i className='iconfont icon-shoucangfill'></i>
						}
						title="收藏"
						key=""
						selected={pathname === '/myshoucang'}
						onPress={() => {
							if (this.props.userinfo == '') {
								this.props.history.push('/login')
							} else {
								this.props.history.push('/myshoucang');
							}

						}}
						data-seed="logId1"
					>
					</TabBar.Item>
					<TabBar.Item
						icon={
							<i className='iconfont icon-dingdanliebiao'></i>
						}
						selectedIcon={
							<i className='iconfont icon-dingdanliebiao1'></i>
						}
						title="订单列表"
						key="orderlist"
						selected={pathname === '/orderlist'}
						onPress={() => {
							this.props.history.push('/orderlist');
						}}
					>

					</TabBar.Item>
					<TabBar.Item
						icon={
							<i className='iconfont icon-wode'></i>
						}
						selectedIcon={
							<i className='iconfont icon-wodedangxuan'></i>
						}
						title="我的淘宝"
						key="mytaobao"
						selected={pathname === '/mytaobao'}
						onPress={() => {
							this.props.history.push('/mytaobao');
						}}
					>

					</TabBar.Item>
				</TabBar>
			</div>
		);
	}
}
export default connect(store => store)(withRouter(Mytab))