import React from 'react';
import { HashRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import MyShouCang from './taobao/MyShouCang';
import Orderlist from './taobao/Orderlist';
import Search from './taobao/Search';
import Detail from './taobao/Detail';
import { Provider } from 'react-redux';
import store from './redux/store'
import MyTaoBao from './taobao/MyTaoBao';
import Login from './taobao/Login'
const App = () => {
	return <Provider store={store}>
		<Router>
			<Switch>
				<Route exact path='/' component={Home} />
				<Route path='/myshoucang' component={MyShouCang} />
				<Route path='/orderlist' component={Orderlist} />
				<Route path='/mytaobao' component={MyTaoBao} />
				<Route path='/home/search' component={Search} />
				<Route path='/home/detail' component={Detail} />
				<Route path='/login' component={Login}/>
				<Route render={() => <Redirect to='/' />} />
			</Switch>
			<div style={{ height: 50 + 'px' }}></div>
			<Mytab />
		</Router>
	</Provider>
}
export default App;

