import React from 'react'
import { Flex } from 'antd-mobile';
import { connect } from 'react-redux';
import {delShouCang} from '../redux/actionCreators'
const ShouCangSize = (props) => {
    console.log(props);
    return (
        <div className="flex-container">
            <Flex style={{backgroundColor:'white',marginBottom:10+'px'}}>
                <Flex.Item><img src={props.data.img} className='shopImg' style={{height:100+'px',width:100+'px'}} ></img></Flex.Item>
                <Flex.Item>
                    <Flex style={{marginBottom:10+'px'}}>{props.data.title}</Flex>
                    <Flex style={{color:'orange'}}>￥ {props.data.price}</Flex>
                </Flex.Item>
                <Flex.Item>
                    <button onClick={()=>{
                        props.dispatch(delShouCang(props.data.id))
                    }}>删除</button>
                </Flex.Item>
            </Flex>
        </div>
    )
}
export default connect()(ShouCangSize);